.newsletter {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 3rem; /* Increased padding for a larger appearance */
    background: linear-gradient(180deg, #fde1ff, #e1ffea 60%);
    border-radius: 16px;
    max-width: 100%;
    width: 90%;
    margin: 0 auto;
    box-shadow: 0px 6px 12px rgba(0, 0, 0, 0.15); /* Slightly larger shadow */
}

.newsletter h1 {
    font-size: 2.2rem; /* Slightly larger font */
    color: #333;
    margin-bottom: 1rem;
    text-align: center;
}

.newsletter p {
    font-size: 1.2rem; /* Slightly larger font */
    color: #666;
    margin-bottom: 2rem;
    text-align: center;
}

.newsletter-input-container {
    display: flex;
    align-items: center;
    width: 80%; /* Slightly wider input/button container */
}

.newsletter input[type="email"] {
    padding: 1rem; /* Increased padding */
    font-size: 1.1rem;
    border: 1px solid #ddd;
    border-radius: 50px 0 0 50px;
    flex: 1;
    outline: none;
    text-align: center;
}

.newsletter button {
    padding: 1rem 2.5rem; /* Increased padding */
    font-size: 1.1rem;
    color: #fff;
    background-color: #000;
    border: none;
    border-radius: 0 50px 50px 0;
    cursor: pointer;
    transition: background-color 0.3s;
}

.newsletter button:hover {
    background-color: #333;
}
