.offers {
    width: 80%; /* Increased width for better visibility */
    max-width: 1200px; /* Set max width to maintain readability on larger screens */
    height: 70vh; /* Slightly taller for larger screens */
    display: flex;
    margin: auto;
    margin-top: 50px;
    padding: 0px 80px; /* Reduced padding for balance */
    margin-bottom: 100px;
    background: linear-gradient(180deg, #fde1ff 0%, #e1ffea22 60%);
    border-radius: 20px; /* Adding subtle rounded corners */
    box-shadow: 0px 4px 20px rgba(0, 0, 0, 0.1); /* Soft shadow for depth */
}

.offers-left {
    flex: 1;
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: 20px; /* Padding for better spacing */
}

.offers-left h1 {
    color: #171717;
    font-size: 3.5vw; /* Responsive font size */
    font-weight: 700; /* Increased weight for emphasis */
    line-height: 1.2;
    margin-bottom: 20px;
}

.offers-left p {
    color: #171717;
    font-size: 1.2rem;
    font-weight: 500;
    margin-bottom: 30px;
}

.offers-left button {
    width: 260px;
    height: 60px;
    border-radius: 30px;
    background-color: red;
    border: none;
    color: white;
    font-size: 20px;
    font-weight: 600;
    cursor: pointer;
    transition: transform 0.2s, background-color 0.2s;
}

.offers-left button:hover {
    background-color: darkred;
    transform: scale(1.05); /* Scale up slightly on hover */
}

.offers-right {
    flex: 1;
    display: flex;
    align-items: center;
    justify-content: flex-end;
    padding-top: 50px;
    overflow: hidden;
}

.offers-right img {
    max-width: 100%; /* Ensures image scales within container */
    height: auto;
    border-radius: 10px; /* Optional: rounded corners for image */
}

/* Media Query for Smaller Screens */
@media (max-width: 768px) {
    .offers {
        flex-direction: column;
        height: auto;
        padding: 20px;
    }

    .offers-left h1 {
        font-size: 7vw; /* Scale heading down for small screens */
    }

    .offers-left p {
        font-size: 1rem;
    }

    .offers-left button {
        width: 220px;
        height: 50px;
        font-size: 18px;
    }

    .offers-right {
        justify-content: center;
        padding-top: 20px;
    }
}
