.footer {
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: #f0f0f0;
    padding: 2rem 0;
    color: #333;
}

.footer-logo {
    display: flex;
    align-items: center;
    margin-bottom: 1.5rem;
}

.footer-logo img {
    width: 50px;
    height: 50px;
    margin-right: 10px;
}

.footer-logo p {
    font-size: 1.5rem;
    font-weight: bold;
    color: #333;
}

.footer_links {
    display: flex;
    justify-content: center;
    list-style: none;
    padding: 0;
    margin-bottom: 1.5rem;
    flex-wrap: wrap;
}

.footer_links li {
    margin: 0 15px;
    cursor: pointer;
    transition: color 0.3s ease;
}

.footer_links li:hover {
    color: #007bff;
}

.footer-social-icons {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 1.5rem;
}

.footer-icons-container {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 40px;
    height: 40px;
    background-color: #333;
    border-radius: 50%;
    margin: 0 8px;
    cursor: pointer;
    transition: background-color 0.3s ease;
}

.footer-icons-container img {
    width: 20px;
    height: 20px;
    filter: invert(100%) sepia(0%) saturate(0%) hue-rotate(93deg) brightness(103%) contrast(103%);
}

.footer-icons-container:hover {
    background-color: #007bff;
}

.footer-copyright {
    margin-top: 1.5rem;
    text-align: center;
    width: 100%;
}

.footer-copyright hr {
    border: none;
    border-top: 1px solid #ddd;
    margin-bottom: 1rem;
    width: 80%;
}

.footer-copyright p {
    font-size: 0.9rem;
    color: #666;
}

/* Responsive Design */
@media (max-width: 768px) {
    .footer_links {
        flex-direction: column;
        align-items: center;
    }

    .footer_links li {
        margin: 10px 0;
    }

    .footer-social-icons {
        flex-direction: row;
        justify-content: center;
    }

    .footer-icons-container {
        margin: 0 8px;
    }
}